<template>
  <section class="hero">
    <div class="hero-body">
      <div class="container">

        <article v-if="announcement" :class="'message ' + announcement.type">
          <div class="message-body">
            <span v-html="announcement.message"></span>
          </div>
        </article>        

        <div class="columns is-fullhd">
          <div class="column is-9">

            <article v-if="err" class="message danger">
              <div class="message-body">
                <span>El servidor no respondió como se esperaba. Por favor vuelva a intentar en unos minutos. <a href="#" @click="solicitarNuevo">Probar de nuevo</a></span>
              </div>
            </article>

            <article v-if="sent" class="message success">
              <div class="message-body">
                <span>Gracias por elegirnos. Tu solicitud fue procesada con éxito, en breve estaremos confirmando la misma. <a href="#" @click="solicitarNuevo">Solicitar nuevo turno</a></span>
              </div>
            </article>

            <div class="app__search revealer has-text-left" :class="{ 'apply' : !loading && !sent && !err }">
              <h2 class="has-text-white">Seleccione vehículo, servicio y sucursal por la que desea ser atendido.</h2>
              <div class="columns" :class="{ 'disabled' : sending }">
                <div class="column is-half">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : search.vehicle }">
                    <span class="icon">
                      <img src="/static/img/icon-car.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Modelo Vehículo</span></li>
                    <li>
                      <span v-if="search.vehicle" :class="{ 'has-text-info' : !search.vehicle, 'has-text-success' : search.vehicle }" v-html="search.vehicle.title"></span>
                      <!--span v-else class="has-text-grey"></span-->
                    </li>
                    </ul>
                  </div>
                  <v-select v-model="search.vehicle" maxHeight="inherit" placeholder="Seleccione su modelo" :selected="search.vehicle" :options="filtroModelos" label="title">
                    <template slot="no-options">
                      Elija modelo
                    </template>                
                    <template slot="option" slot-scope="option">
                      <div class="d-center">
                        <strong>{{ option.title }}</strong>
                      </div>
                    </template>
                    <template slot="selected-option" scope="option">
                      <div class="selected d-center">
                        <!-- {{ option.city }} ({{ option.airport_code }}) -->
                      </div>
                    </template>                  
                  </v-select>                
                </div> 
                <div class="column toggle-type is-half">
                  <div class="columns" value="vehicle_type">
                    <div class="column button is-mobile is-large" value="auto" :class="{ 'is-white' : search.vehicle_type != 'auto' }" @click="setToggleType">Auto</div>
                    <div class="column button is-mobile is-large" value="vito" :class="{ 'is-white' : search.vehicle_type != 'vito' }" @click="setToggleType">Vito</div>
                    <div class="column button is-mobile is-large" value="sprinter" :class="{ 'is-white' : search.vehicle_type != 'sprinter' }" @click="setToggleType">Sprinter</div>
                    <div class="column button is-mobile is-large" value="truck" :class="{ 'is-white' : search.vehicle_type != 'truck' }" @click="setToggleType">Camión</div>
                  </div>
                </div>
              </div>
              <div class="columns" :class="{ 'disabled' : sending }">
                <div class="column is-half">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : search.dealer }">
                    <span class="icon">
                      <img src="/static/img/icon-building.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                      <li><span>Sucursal</span></li>
                      <li>
                        <span v-if="search.dealer" :class="{ 'has-text-info' : !search.dealer, 'has-text-success' : search.dealer }" v-html="search.dealer.nombre"></span>
                        <!--span v-else class="has-text-grey" jsb-word="search-origin-placeholder"></span-->
                      </li>
                    </ul>
                  </div>
                  <v-select v-model="search.dealer" maxHeight="inherit" @input="setDealer" placeholder="Seleccione Sucursal" :options="filtroDealers" label="nombre">
                    <template slot="no-options">
                      Seleccione sucursal
                    </template>                
                    <template slot="option" slot-scope="option">
                      <div class="d-center">
                        <strong>{{ option.nombre }}</strong> {{ option.localidad }}<br>
                        ({{ option.direccion }})
                      </div>
                    </template>
                    <template slot="selected-option" scope="option">
                      <div class="selected d-center">
                        <!-- {{ option.city }} ({{ option.airport_code }}) -->
                      </div>
                    </template>    
                  </v-select>
                </div>

                <div class="column toggle-type is-half">
                  <div class="columns" value="service_type">
                    <div class="column button is-large" value="reparacion" :class="{ 'is-white' : search.service_type != 'reparacion' }" @click="setToggleType">Turno Taller</div>
                    <div class="column button is-large" value="reposiciones-seguros" :class="{ 'is-white' : search.service_type != 'reposiciones-seguros' }" @click="setToggleType">Seguros</div>
                    <div class="column button is-large" value="repuestos" :class="{ 'is-white' : search.service_type != 'repuestos' }" @click="setToggleType">Repuestos</div>
                  </div>
                </div>
              </div>
              <div class="columns" :class="{ 'disabled' : sending }">
                <div class="column is-one-quarter">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : validateName(search.full_name) }">
                    <span class="icon">
                      <img src="/static/img/pasengers_icon.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Nombre completo</span></li>
                    <li><input type="text" v-model="search.full_name" class="has-text-info" /></li>
                    </ul>
                  </div>
                </div>

                <div class="column is-one-quarter">
                  <!--div class="button is-large is-white is-fullwidth" @click="showPassengers">
                    <span class="icon">
                      <img src="/static/img/pasengers_icon.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Con quién venís?</span></li>
                    <li><span class="has-text-info" v-html="passengersText"></span></li>
                    </ul>
                  </div>
                  <passengerpicker :class="{ 'is-active' : passengersToggle }" v-bind="search.passengers" @passengerchanged="handlePassengersUpdate($event)" @click="showPassengers"></passengerpicker-->
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : validateEmail(search.email) }">
                    <span class="icon">
                      <img src="/static/img/icono-email.svg">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Email</span></li>
                    <li><input type="email" v-model="search.email" class="has-text-info" /></li>
                    </ul>
                  </div>
                </div>

                <div class="column is-one-quarter category" :class="{ 'disabled' : sending }">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : validatePhone(search.phone) }">
                    <span class="icon">
                      <img src="/static/img/icono-telefono.svg">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Teléfono</span></li>
                    <li><input type="phone" v-model="search.phone" class="has-text-info"/></li>
                    </ul>
                  </div>
                </div>

                <div class="column is-one-quarter" :class="{ 'disabled' : search.service_type != 'repuestos' }">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : search.repuesto }">
                    <span class="icon">
                      <img src="/static/img/category_icon.svg">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Repuesto</span></li>
                    <li><input type="text" v-model="search.repuesto" class="has-text-info" /></li>
                    </ul>
                  </div>
                </div>                
              </div>
              <div class="columns" :class="{ 'disabled' : sending }">
                <div class="column is-one-quarter turno-date">
                  <div class="button is-large is-white is-fullwidth" @click="showDepartDate" :class="{ 'accepted' : search.turno_date }">
                    <span class="icon">
                      <img src="/static/img/calendar_icon.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                    <li><span>Fecha turno</span></li>
                    <li>
                      <span v-if="search.turno_date" class="has-text-info" v-html="search.turno_date"></span>
                      <span v-else class="has-text-grey">mm/dd/aa</span>
                    </li>
                    </ul>
                  </div>
                  <vue-datepicker-local :local="$root.local" :disabled-date="$root.disabledDates" v-model="search.turno_date"></vue-datepicker-local>
                </div>
                <div class="column is-one-quarter turno-time">
                  <div class="button is-large is-white is-fullwidth" :class="{ 'accepted' : search.turno_time }">
                    <span class="icon">
                      <img src="/static/img/time_icon.png">
                    </span>
                    <ul class="is-pulled-left has-text-left">
                      <li><span>Horario</span></li>
                      <li>
                        <span v-if="search.turno_time" :class="{ 'has-text-info' : !search.turno_time, 'has-text-success' : search.turno_time }"  v-html="search.turno_time"></span>
                        <!--span v-else class="has-text-grey">hh:mm</span-->
                      </li>
                    </ul>
                  </div>
                  <v-select v-model="search.turno_time" :options="schedules" maxHeight="inherit" label="label" placeholder="hh:mm">
                    <template slot="no-options">
                      Seleccione sucursal
                    </template>                
                    <template slot="option" slot-scope="option">
                      <div class="d-center">
                        <strong>{{ option.label }}</strong>
                      </div>
                    </template>
                    <template slot="selected-option" scope="option">
                      <div class="selected d-center">
                        <!-- {{ option.label }} -->
                      </div>
                    </template>
                  </v-select>
                </div>

                <div class="column is-half has-text-right">
                  <button class="button is-expanded" @click="submit" :disabled="!canShowResults" :class="{ 'is-loading' : sending }">Solicitar turno</button>
                  </router-link>
                </div>
              </div>            
            </div>
          </div>
          <div v-for="ad in ads" class="column is-flex-column has-text-centered">
            <a :href="ad.url" target="_blank">
              <img class="ad" :src="ad.image" />
            </a>
          </div>
        </div>
      </div>
      <div v-if="offers && offers.length" class="container offers">
        <div class="columns">
          <div class="column has-text-left">
            <h1 class="has-text-info">Conozca nuestras sucursales</h1>
          </div>
        </div>
        <div class="columns is-multiline">
          <div v-for="offer in offers" class="column is-3">
            <a class="offer" :href="offer.url">
              <div class="bg-inline" :style="'background-image:url(' + offer.image + ')'">
              </div>
              <div class="has-text-left">
                <h1 class="has-text-info" v-html="offer.title"></h1>
                <h6 class="has-text-dark" v-html="offer.subtitle"></h6>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
import qs from 'qs';
import _ from 'lodash';
import helper from '@/components/Helper';
import { snackbar } from '@/components/Snackbar';

export default {
  name: 'TurnosKlasse',
  data () {
    return {
      time: new Date(),
      err:false,
      sent:false,
      sending:false,
      announcement:{},
      loading:true,
      models:[],
      dealers:[],
      ads:{},
      schedules:[],
      offers:{},
      texts:{},      
      canShowResults:false,
      passengersToggle:false,
      passengersText:'',
      search: {
        vehicle: null,
        vehicle_type: 'auto',
        service_type: 'reparacion',
        turno_date:null,
        turno_time:null,
        dealer:null
      },    
      disabledDates: {
        to: new Date()
        //from: new Date(2016, 0, 26)
      }
    }
  },  
  mounted: function(){
    var self = this;
    var storage = JSON.parse(localStorage.getItem('storage'));

    if(storage){
      this.handleStorageUpdate(storage);
      this.announcement = storage.announcement;
    }

    if(!this.models.length) {
        axios.get( `/static/dummy/mercedes-models.json`)
          .then(res => {
            self.models = res.data;
          }).catch(function(error) {
            console.log(error)
          })      
    }

    if(!this.dealers.length) {
        axios.get( `/static/dummy/mercedes-dealers.json`)
          .then(res => {
            self.dealers = res.data;
          }).catch(function(error) {
            console.log(error)
          })      
    }

    this.$root.$on('storagechanged',() => this.handleStorageUpdate(this.storage));

    setTimeout(() => this.handlePassengersUpdate(this.search.passengers), 500);
    setTimeout(() => self.loading = false, 700);    
  },
  computed: {
    filtroModelos() {
      //this.search.vehicle = null;
      return _.filter(JSON.parse(JSON.stringify(this.models)),(o) => {
        return o.vehicle_type === this.search.vehicle_type
      })
    },
    filtroDealers() {
      //this.search['dealer'] = null;
      return _.filter(JSON.parse(JSON.stringify(this.dealers)),(o) => {
        return o.servicios[this.search.vehicle_type] && o.servicios[this.search.vehicle_type].indexOf(this.search.service_type) > -1
      })
    }
  },
  watch: {
    search: {
      handler: function(search, oldSearch) {
        this.handleSearchUpdate(search)
      },
      immediate: true,
      deep: true
    }
  },
  methods : {
    handleStorageUpdate: function(storage){
      console.log("updating storage from home")
      this.offers = storage.offers.collection;
      if(storage.ads && storage.ads.collection){
        this.ads = storage.ads.collection.slice(1);
      }
    },
    handleSearchUpdate: function(search){
      this.canShowResults = false;
      if(this.search.turno_date){
        this.search.turno_date = moment(this.search.turno_date).format('YYYY-MM-DD');
      }

      if( search.turno_date && 
          search.turno_time && 
          search.dealer && 
          this.validateEmail(search.email) && 
          this.validateName(search.full_name) && 
          this.validatePhone(search.phone)){          
          this.canShowResults = true;
      }
    },
    handlePassengersUpdate: function(passengers) {
      var info = [];
      var text = '';
      for(var i in passengers){
        const qty = passengers[i];
        if(qty){
          var word = i;
          word = qty == 1 ? word.replace('res','r').replace('ren','').replace('s','') : word ;
          info.push([qty,word].join(' '));
        }
      }
      if(info.length){
        text = info.join(',');
        text = text.length > 23 ? text.substr(0,23) + '…' : text;
      } else {
        text = 'search-passengers-placeholder';
      }
      this.search.passengers = passengers;
      this.passengersText = text;
    },
    validateEmail: function(email) {
      var re = /\S+@\S+\.\S+/;
      return re.test(email);
    },
    validateName: function(email) {
      var re = /\S+ \S+/;
      return re.test(email);
    },
    validatePhone: function(email) {
      var re = /\d+ \d+/;
      return re.test(email);
    },
    setToggleType: function({type, target}){
      this.search[target.parentNode.getAttribute("value")] = target.getAttribute("value");
    },
    setDealer: function(dealer){
      if(dealer && typeof dealer == 'object'){
        this.schedules = JSON.parse(JSON.stringify(dealer.horarios))
      }
    },
    solicitarNuevo: function(){
      this.err = false
      this.sent = false
      this.search.vehicle = null
      this.search.vehicle_type =  'auto'
      this.search.service_type = 'reparacion'
      this.search.turno_date = null
      this.search.turno_time = null
      this.search.full_name = null
      this.search.email = null
      this.search.phone = null
      this.search.repuesto = null
      this.search.dealer = null
    },
    showDepartDate: function({type,target}){
      document.querySelector(".turno-date input").click();
    },
    showReturnDate: function({type,target}){
      document.querySelector(".turno-time input").click();
    },
    showPassengers: function(){
      this.passengersToggle = !this.passengersToggle;
      //document.getElementById('passengers').classList.toggle('is-active');
    },
    showCategory: function(){
    },
    submit : function({type, target}){
      const that = this
      this.sending = true;
      axios.post( `https://api.automovilshop.com/pilot`,this.search)
      //axios.post( `https://automovilshop.api/pilot`,this.search)
        .then(res => {
          this.sending = false;
          if(res.data.status==='success'){
            that.sent = true
          } else {
            that.err = true
          }
        }).catch(function(error) {
          console.log(error)
        })
    }
  }
}
</script>



